import config from 'app-config'

export const initialState = {
  pendingQueue: [],
}

const getNetworkKeyByVersion = (networkID) => {

  for (const key in config.evmNetworks) {
    if (config.evmNetworks[key].chainId === networkID) {
      return key;
    }
  }
  return null; // Or some default value if no match is found
};

export const addTransactionToQueue = (state, { networkID, hash }) => {

  const networkCoin = getNetworkKeyByVersion(networkID);

  const networkData = config.evmNetworks[networkCoin?.toUpperCase() ?? '']

  return {
    ...state,
    pendingQueue: [
      ...state.pendingQueue,
      {
        networkData,
        hash,
      },
    ],
  }
}

export const removeLastTransactionFromQueue = (state) => {
  const newQueue = state.pendingQueue.slice(1)


  return {
    ...state,
    pendingQueue: newQueue,
  }
}
